import React from 'react';
import logo from './images/logo.png'
import {NavLink} from 'react-router-dom'

const Navbar = () => {
    return (
        <>
            {/* <div className="">
                <div className="row">
                    <div className="mx-auto"> */}


            <nav class="navbar navbar-expand-lg navbar-light bg-light">
                <div class="container-fluid">
                    <NavLink className="navbar-brand" to="/"> <img className='logo' src={logo} alt="Logo" /></NavLink>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav new mb-2 mb-lg-0">
                            <li className="nav-item">
                                <NavLink className="nav-link"  to="/home">HOME</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" aria-current="page" to="/about">ABOUT US</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/services">SERVICES</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/freelancers">FREELANCERS</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/blog">BLOG</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/tutorial">TUTORIAL</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/contact">CONTACT US</NavLink>
                            </li>
                            <button className="btn btn-outline-success custombtn1" type="submit">Log in/Sign up</button>

                        </ul>

                    </div>
                </div>
            </nav>
            {/* </div>

                </div>

            </div> */}
        </>
    );
};


export default Navbar;